.about-us-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin-top: 100px;
  }
  
  .thumbnails {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
    margin-top:30px;
  }
  
  .thumbnail {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    margin:30px
  }
  
  .thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  h1 {
    text-align: center;
  }
  .thumbnail-image-hover {
    display: none;
  }
  
  .thumbnail:hover .thumbnail-image {
    display: none;
  }
  
  .thumbnail:hover .thumbnail-image-hover {
    display: block;
  }